<script>
import service from "../../utils/request";
export default {
  name: "Banner",
  data() {
    return {
      // vod-pic 图片地址
      // vod_year年份   vod_area地区 国家   vod_class类型标签   vod_lang语言  vod_score评分 vod_content影片介绍
      // vod_remarks 清晰度  vod_hits点击次数 vod_time最后更新时间  vod_blurb宣传语
      // vod_director 导演    vod_actor 主演演员   vod_en 又名
      listBanner: [],
      tuijian:[],
      tuijian2:[],
    };
  },
  methods: {},
  mounted() {
    service.get("/api/index/banner")
    .then((result) => {
        // console.log("about", result.data);
        this.listBanner = result.data;
      }).catch((err) => {
        console.log(err);
      });
  },
};
