<template>
  <div>
    <Banner></Banner>
    <div class="titlediv">
      <div class="H_title"><span>电影推荐</span></div>
      <div style="width: 60px">
        <router-link to="/mv">
          <button class="btn">更多<i class="el-icon-arrow-right"></i></button>
        </router-link>
      </div>
    </div>
    <ListVideo :List="MvList" :LieShu="2"></ListVideo>

    <div class="titlediv">
      <div class="H_title"><span>剧集推荐</span></div>
      <div style="width: 60px">
        <router-link to="/tv">
          <button class="btn">更多<i class="el-icon-arrow-right"></i></button>
        </router-link>
      </div>
    </div>
    <ListVideo :List="TvList" :LieShu="2"></ListVideo>

    <div class="titlediv">
      <div class="H_title"><span>综艺推荐</span></div>
      <div style="width: 60px">
        <router-link to="/va">
          <button class="btn">更多<i class="el-icon-arrow-right"></i></button>
        </router-link>
      </div>
    </div>
    <ListVideo :List="VaList" :LieShu="2"></ListVideo>

    <div class="titlediv">
      <div class="H_title"><span>动漫推荐</span></div>
      <div style="width: 60px">
        <router-link to="/ac">
          <button class="btn">更多<i class="el-icon-arrow-right"></i></button>
        </router-link>
      </div>
    </div>
    <ListVideo :List="AcList" :LieShu="2"></ListVideo>
  </div>
</template>

<script>
import Banner from "./Banner";
import ListVideo from "./ListVideo";
import service from "../../utils/request";
export default {
  name: "Vindex",
  components: { Banner, ListVideo },
  data() {
    return {
      MvList: [],
      TvList: [],
      VaList: [],
      AcList: [],
    };
  },
  mounted() {
    // 获得mvlist电影
    document.title = "小柏影视";
    service
      .get("/api/index/mv")
      .then((result) => {
        this.MvList = result.data;
      })
      .catch((err) => {
        console.log(err);
      });
    setTimeout(() => {
      // TvList
      service
        .get("/api/index/tv")
        .then((result) => {
          this.TvList = result.data;
        })
        .catch((err) => {
          console.log(err);
        });
    }, 200);
    setTimeout(() => {
      // VaList
      service
        .get("/api/index/va")
        .then((result) => {
          this.VaList = result.data;
        })
        .catch((err) => {
          console.log(err);
        });
    }, 500);
    setTimeout(() => {
      // AcList
      service
        .get("/api/index/ac")
        .then((result) => {
          this.AcList = result.data;
        })
        .catch((err) => {
          console.log(err);
        });
    }, 700);
  },
};
</script>

<style scoped>
.H_title {
  font-size: 20px;
  color: #333;
  width: 1200px;
}
.titlediv {
  display: flex;
  justify-content: center;
}
.btn {
  margin-left: -10px;
  background-color: #fff;
  font-size: 13px;
  line-height: 23px;
  border-radius: 2px;
  border: 1px solid var(--br2);
  padding: 1px 10px 1px 10px;
  text-align: center;
}
.el-icon-arrow-right {
  width: 10px;
}

@media screen and (max-width: 1261px) {
  .H_title {
    font-size: 20px;
    color: #333;
    width: 95%;
  }
}
@media screen and (max-width: 1100px) {
  .H_title {
    width: 90%;
  }
  .titlediv{
    margin-top: 10px;
  }
}
</style>